import React, { useState, useEffect } from 'react';
import { fetchPapers, fetchCategories } from '../services/api';
import './Papers.css';

function Papers() {
  const [papers, setPapers] = useState([]);
  const [categories, setCategories] = useState([]);
  const [selectedCategory, setSelectedCategory] = useState('cs');
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);
  const [searchTerm, setSearchTerm] = useState('');

  useEffect(() => {
    // 
    const getCategories = async () => {
      try {
        const data = await fetchCategories();
        setCategories(data);
      } catch (err) {
        console.error('Error fetching categories:', err);
      }
    };
    
    getCategories();
  }, []);

  useEffect(() => {
    // 
    const getPapers = async () => {
      setLoading(true);
      try {
        const data = await fetchPapers(selectedCategory);
        setPapers(data);
        setError(null);
      } catch (err) {
        console.error('Error fetching papers:', err);
        setError('');
        setPapers([]);
      } finally {
        setLoading(false);
      }
    };
    
    getPapers();
  }, [selectedCategory]);

  // 
  const filteredPapers = papers.filter(paper => 
    paper.title.toLowerCase().includes(searchTerm.toLowerCase()) || 
    paper.summary.toLowerCase().includes(searchTerm.toLowerCase())
  );

  return (
    <div className="papers-page">
      <h1></h1>
      
      <div className="papers-controls">
        <div className="category-selector">
          <label htmlFor="category"></label>
          <select 
            id="category" 
            value={selectedCategory}
            onChange={(e) => setSelectedCategory(e.target.value)}
          >
            {categories.map(category => (
              <option key={category.id} value={category.id}>
                {category.name}
              </option>
            ))}
          </select>
        </div>
        
        <div className="search-box">
          <input
            type="text"
            placeholder="..."
            value={searchTerm}
            onChange={(e) => setSearchTerm(e.target.value)}
          />
        </div>
      </div>

      {loading ? (
        <div className="loading">...</div>
      ) : error ? (
        <div className="error">{error}</div>
      ) : (
        <div className="papers-list">
          {filteredPapers.length === 0 ? (
            <p></p>
          ) : (
            filteredPapers.map((paper, index) => (
              <div key={index} className="paper-card">
                <h3 className="paper-title">{paper.title}</h3>
                <p className="paper-authors">
                  : {paper.authors.join(', ')}
                </p>
                <p className="paper-date">
                  : {new Date(paper.published).toLocaleDateString()}
                </p>
                <p className="paper-summary">
                  {paper.summary.length > 300
                    ? paper.summary.substring(0, 300) + '...'
                    : paper.summary}
                </p>
                <div className="paper-footer">
                  <a 
                    href={paper.link} 
                    target="_blank" 
                    rel="noopener noreferrer"
                    className="paper-link"
                  >
                    
                  </a>
                </div>
              </div>
            ))
          )}
        </div>
      )}
    </div>
  );
}

export default Papers; 