import React, { useState, useEffect } from 'react';
import { fetchCategories, subscribeUser, testKeywordMatch } from '../services/api';
import { isConfigured } from '../services/localData';
import { Link } from 'react-router-dom';
import { Alert } from 'react-bootstrap';
import './Subscribe.css';

function Subscribe() {
  const [email, setEmail] = useState('');
  const [keywords, setKeywords] = useState('');
  const [selectedCategories, setSelectedCategories] = useState(['cs']);
  const [categories, setCategories] = useState([]);
  const [loading, setLoading] = useState(false);
  const [message, setMessage] = useState(null);
  const [messageType, setMessageType] = useState('');
  const [testResults, setTestResults] = useState([]);
  const [showTestResults, setShowTestResults] = useState(false);
  const [useOpenAI, setUseOpenAI] = useState(true);
  const [configured, setConfigured] = useState(false);

  useEffect(() => {
    // 
    const getCategories = async () => {
      try {
        const data = await fetchCategories();
        setCategories(data);
      } catch (err) {
        console.error('Error fetching categories:', err);
      }
    };
    
    getCategories();
    
    // API
    setConfigured(isConfigured());
  }, []);

  const handleCategoryChange = (categoryId) => {
    setSelectedCategories(prev => {
      if (prev.includes(categoryId)) {
        return prev.filter(id => id !== categoryId);
      } else {
        return [...prev, categoryId];
      }
    });
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    
    if (!email || !keywords) {
      setMessage('');
      setMessageType('error');
      return;
    }
    
    if (selectedCategories.length === 0) {
      setMessage('');
      setMessageType('error');
      return;
    }
    
    const keywordsList = keywords
      .split(',')
      .map(k => k.trim())
      .filter(k => k.length > 0);
    
    if (keywordsList.length === 0) {
      setMessage('');
      setMessageType('error');
      return;
    }
    
    setLoading(true);
    try {
      await subscribeUser(email, keywordsList, selectedCategories);
      setMessage('');
      setMessageType('success');
      
      // 
      setEmail('');
      setKeywords('');
      setSelectedCategories(['cs']);
    } catch (err) {
      console.error('Subscription error:', err);
      setMessage(err.message || '');
      setMessageType('error');
    } finally {
      setLoading(false);
    }
  };

  const handleTestKeywords = async () => {
    if (!keywords) {
      setMessage('');
      setMessageType('error');
      return;
    }
    
    if (useOpenAI && !configured) {
      setMessage('AIOpenAI API');
      setMessageType('error');
      return;
    }
    
    const keywordsList = keywords
      .split(',')
      .map(k => k.trim())
      .filter(k => k.length > 0);
    
    setLoading(true);
    try {
      const response = await testKeywordMatch(keywordsList, useOpenAI);
      setTestResults(response);
      setShowTestResults(true);
      setMessage('');
      setMessageType('success');
    } catch (err) {
      console.error('Keyword test error:', err);
      setMessage(err.message || '');
      setMessageType('error');
    } finally {
      setLoading(false);
    }
  };

  return (
    <div className="subscribe-page">
      <h1></h1>
      
      {message && (
        <div className={`message ${messageType}`}>
          {message}
        </div>
      )}
      
      {useOpenAI && !configured && (
        <Alert variant="warning" className="mb-4">
          OpenAI APIAI
          <Link to="/settings" className="alert-link mx-1"></Link>
          
        </Alert>
      )}
      
      <div className="subscribe-container">
        <div className="subscribe-form-container">
          <form onSubmit={handleSubmit} className="subscribe-form">
            <div className="form-group">
              <label htmlFor="email"> *</label>
              <input
                type="email"
                id="email"
                value={email}
                onChange={(e) => setEmail(e.target.value)}
                placeholder="your.email@example.com"
                required
              />
            </div>
            
            <div className="form-group">
              <label htmlFor="keywords"> *</label>
              <textarea
                id="keywords"
                value={keywords}
                onChange={(e) => setKeywords(e.target.value)}
                placeholder=", , "
                required
              />
              <small></small>
            </div>
            
            <div className="form-group">
              <label> *</label>
              <div className="categories-container">
                {categories.map(category => (
                  <div key={category.id} className="category-checkbox">
                    <input
                      type="checkbox"
                      id={`category-${category.id}`}
                      checked={selectedCategories.includes(category.id)}
                      onChange={() => handleCategoryChange(category.id)}
                    />
                    <label htmlFor={`category-${category.id}`}>{category.name}</label>
                  </div>
                ))}
              </div>
            </div>
            
            <div className="form-group">
              <div className="ai-option">
                <input
                  type="checkbox"
                  id="use-openai"
                  checked={useOpenAI}
                  onChange={() => setUseOpenAI(!useOpenAI)}
                />
                <label htmlFor="use-openai">AI</label>
              </div>
            </div>
            
            <div className="form-actions">
              <button 
                type="button" 
                onClick={handleTestKeywords}
                disabled={loading}
                className="btn btn-secondary"
              >
                
              </button>
              <button 
                type="submit" 
                disabled={loading}
                className="btn btn-primary"
              >
                {loading ? '...' : ''}
              </button>
            </div>
          </form>
        </div>
        
        {showTestResults && (
          <div className="test-results-container">
            <h2></h2>
            {testResults.length === 0 ? (
              <p></p>
            ) : (
              <>
                <p> {testResults.length} </p>
                <div className="test-papers-list">
                  {testResults.map((paper, index) => (
                    <div key={index} className="test-paper-item">
                      <h3>{paper.title}</h3>
                      <p className="paper-authors">: {Array.isArray(paper.authors) ? paper.authors.join(', ') : paper.authors}</p>
                      <p className="paper-summary">
                        {paper.summary.substring(0, 200)}...
                      </p>
                      <p className="paper-matched-keywords">
                        : {paper.matched_keywords.join(', ')}
                      </p>
                      <a 
                        href={paper.link} 
                        target="_blank" 
                        rel="noopener noreferrer"
                        className="paper-link"
                      >
                        
                      </a>
                    </div>
                  ))}
                </div>
              </>
            )}
            <button 
              onClick={() => setShowTestResults(false)}
              className="btn btn-secondary"
            >
              
            </button>
          </div>
        )}
      </div>
    </div>
  );
}

export default Subscribe; 