/**
 * 
 * @param {string} to - 
 * @param {Array} papers - 
 * @returns {Promise<Object>} - 
 */
export const sendEmailNotification = async (to, papers) => {
  try {
    const emailSettings = localStorage.getItem('email_settings');
    if (!emailSettings) {
      throw new Error('');
    }
    
    const settings = JSON.parse(emailSettings);
    const { emailjsId, serviceId, templateId } = settings;
    
    if (!window.emailjs) {
      throw new Error('EmailJS');
    }
    
    // 
    const papersHTML = papers.map(paper => `
      <div style="margin-bottom: 20px; padding: 15px; border: 1px solid #eee; border-radius: 5px;">
        <h3><a href="${paper.link}" target="_blank">${paper.title}</a></h3>
        <p><strong>:</strong> ${paper.authors.join(', ')}</p>
        <p><strong>:</strong> ${new Date(paper.published).toLocaleDateString()}</p>
        <p><strong>:</strong> ${paper.matched_keywords.join(', ')}</p>
        <p>${paper.summary.substring(0, 200)}...</p>
      </div>
    `).join('');
    
    // 
    const response = await window.emailjs.send(
      serviceId,
      templateId,
      {
        to_email: to,
        subject: 'arXiv',
        papers: papersHTML
      },
      emailjsId
    );
    
    return { 
      success: true, 
      message: '',
      response
    };
  } catch (error) {
    console.error('Error sending email notification:', error);
    throw new Error(`: ${error.message}`);
  }
};

